import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import actions from './actions';
import actionHandlers from './action-handlers';
import {gFormActionHandlers} from './gFormActions';
import {SC_CAT_ITEM, REQUEST_GENERATED} from './constants';
const name = 'sn-catalog-form-connected';
/**
 * Data Provider component that understands how to fetch data and present it on properties.
 *
 * ```jsx
 * <sn-catalog-form-connected
 *		actionDetails={...}
 *		attachmentDetails={...}
 *		catalogItemDetails={...}
 *		deliveryInformation={...}
 *      errorMessage={...}
 *		formData={...}
 *		hideRequestedFor={...}
 *		isError={...}
 *      isLoading={...}
 *		isSuccess={...}
 *		priceDetails={...}
 *      quantityDetails={...}
 *		requestedFor={...}
 *      requestedForDetails={...}
 *		specialInstructions={...}
 *		sysId={...}
 *      table={...}
 *      targetRecord={...}
 *		variables={...}
 *		variablesLayout={...}
 *		/>
 * ```
 * @summary Data provider component that understands how to fetch data and present it on properties..
 * @seismicElement sn-catalog-form-connected
 */
createCustomElement(name, {
	renderer: {
		type: snabbdom,
		view: () => {
			return <slot />;
		}
	},
	properties: {
		/**
		 * User action (Submit/Order) details
		 * @type {object}
		 */
		actionDetails: {selectable: true, default: {}},
		/**
		 * Attachment type details
		 * @type {object}
		 */
		attachmentDetails: {selectable: true, default: {}},
		/**
		 * Catalog item details
		 * @type {object}
		 */
		catalogItemDetails: {selectable: true, default: {}},
		/**
		 * Delivery information details
		 * @type {object}
		 */
		deliveryInformation: {selectable: true, default: {}},
		/**
		 * Error message details
		 * @type {String}
		 */
		errorMessage: {selectable: true, default: ''},
		/**
		 * true if item has no variables
		 * @type {boolean}
		 */
		hasNoVariables: {selectable: true, default: false},
		/**
		 * Table from which data needs to be fetched
		 * @type {object}
		 */
		formData: {
			selectable: true,
			default: {tableName: SC_CAT_ITEM, sysId: -1},
			dependencies: ['catalogItemDetails']
		},
		/**
		 * Hide/Show requested for field
		 * @type {boolean}
		 */
		hideRequestedFor: {selectable: true, default: false},
		/**
		 * Value that indicates whether the component has any error
		 * @type {boolean}
		 */
		isError: {selectable: true, default: false},
		/**
		 * Value that indicates whether the component is in loading state
		 * @type {boolean}
		 */
		isLoading: {selectable: true, default: true},
		/**
		 * Value that indicates whether the component loaded successfully or not
		 * @type {boolean}
		 */
		isSuccess: {selectable: true, default: false},
		/**
		 * catalog item price details
		 * @type {String | Array.<String>}
		 */
		priceDetails: {selectable: true, default: {}},
		/**
		 * catalog item quantity details
		 * @type {String}
		 */
		quantityDetails: {selectable: true, default: {}},
		/**
		 * Requested for user `sys-id`
		 * @type {String}
		 */
		requestedForId: {default: '', selectable: false, reflect: false},
		/**
		 * Requested for user's details
		 * @type {object}
		 */
		requestedForDetails: {selectable: true, default: {}},
		/**
		 * Special instruction while order catalog item
		 * @type {String}
		 */
		specialInstructions: {selectable: true, default: {}},
		/**
		 * Catalog Item `sys-id`
		 * @type {String}
		 */
		sysId: {reflect: true, default: '', selectable: false, required: true},
		/**
		 * table to fetch data
		 * @type {object}
		 */
		table: {
			reflect: false,
			default: SC_CAT_ITEM,
			selectable: false,
			readOnly: true
		},
		/**
		 * Target record details
		 * @type {String}
		 */
		targetRecord: {selectable: true, default: {}},
		/**
		 * Object containing the session user information
		 * @type {userSession}
		 */
		userSession: {},
		/**
		 * Variable details
		 * @type {String}
		 */
		variables: {selectable: true, default: {}},
		/**
		 * Variable layout details (1 column or 2 column)
		 * @type {String}
		 */
		variablesLayout: {selectable: true, default: []}
	},
	actions: {
		...actions,
		...gFormActionHandlers.actions
	},
	actionHandlers: {
		...actionHandlers,
		...gFormActionHandlers.actionHandlers
	},
	dispatches: {
		/**
		 * Dispatched when request generated
		 * @type {{table: String, sysId: String, number: String}}
		 */
		REQUEST_GENERATED: {},
		/**
		 * Dispatched when catalog item has been changed
		 *
		 * @type {{sys_id: string}}
		 */
		CATALOG_ITEM_CHANGED: {},
		/**
		 * Dispatched after fetching catalog item details successfully
		 *
		 * @type {{}}
		 */
		RESOURCES_FETCHED: {},
		/**
		 * Dispatched when error occured while fetching resources
		 *
		 * @type {{}}
		 */
		FAILED_TO_FETCH_RESOURCES: {},
		/**
		 * This will set `g_form`, `variables`, `variablesLayout`, `item-meta-data` to state and turn off loading mode
		 *
		 * @type {{}}
		 */
		FORM_ENVIRONMENT_CREATED: {},
		/**
		 * This will be fired when error occured while creating form environment.
		 *
		 * @type {{ type: string, message: string}}
		 */
		FORM_ENVIRONMENT_CREATION_FAILED: {},
		/**
		 * This will be fired when catalog item is ready to display.
		 *
		 * @type {{ sysId: string}}
		 */
		CATALOG_ITEM_READY: {},
		/**
		 * Dispatched when resource fetching started
		 *
		 * @type {{}}
		 */
		RESOURCES_FETCH_STARTED: {},
		/**
		 * Dispatched when resources processed
		 *
		 * @type {{itemDetails: string, formData: Object}}
		 */
		RESOURCES_PROCESSED: {},
		/**
		 * Dispatched when requested for user has been changed
		 * @type {{user_id:string}}
		 */
		REQUESTED_FOR_CHANGED: {},
		/**
		 * This fired when delivery address has changed of catalog item
		 * @type {{value:string}}
		 */
		DELIVERY_ADDRESS_UPDATED: {},
		/**
		 * This dispatched when delivery address updation failed
		 * @type {{}}
		 */
		DELIVERY_ADDRESS_UPDATE_FAILED: {},
		/**
		 * Dispatched from the environment after onChange handlers are processed
		 *
		 * @type {{ variables: Object }}
		 */
		VARIABLES_UPDATED: {},
		/**
		 * This will update quantity in state and calls appropriate handler based on type of catalog item.
		 * @type {{value: number, previousValue: number}}
		 */
		QUANTITY_UPDATED: {},
		/**
		 * Dispatched when catalog form changed
		 *
		 * @type {{}}
		 */
		SN_CATALOG_FORM_CHANGED: {},
		/**
		 * This is dispatched when Unable to fetch delivery address
		 * @type {{type: number, message: string}}
		 */
		NEW_MESSAGE_ARRIVED: {},
		/**
		 * This is dispatched when any http error occured
		 * @type {{}}
		 */
		HTTP_ERROR_OCCURRED: {},
		/**
		 * Fired when quantity or varaibles that affect price of catalog item
		 * @type {{}}
		 */
		PRICE_AFFECTED: {},
		/**
		 * This will be fired when price of catalog item fetched successfully.
		 *
		 * @type {{}}
		 */
		PRICE_UPDATED: {},
		/**
		 * This will be fired if error occured while fetching price.
		 * @type {{ message: string }}
		 */
		PRICE_UPDATE_FAILED: {},
		/**
		 * Dispatched when user tries to submit/order the catalog item
		 *
		 * @type {{}}
		 */
		SUBMIT_CLICKED: {},
		/**
		 * Dispatched when catalog item order is validated
		 *
		 * @type {{}}
		 */
		SUBMIT_VALIDATED: {},
		/**
		 * Triggered if the submission/ordering process is successful from back-end
		 *
		 * @type {{}}
		 */
		SUBMITTED: {},
		/**
		 * Triggered if submit/order API returns error response
		 *
		 * @type {{}}
		 */
		SUBMIT_FAILED: {}
	},
	slots: {
		/**
		 * The default slot to put content in.
		 * @defaultSlot
		 */
		defaultSlot: {}
	}
});
export default name;
