import {
	REQUESTED_FOR_CHANGED,
	DELIVERY_ADDRESS_UPDATED,
	DELIVERY_ADDRESS_UPDATE_FAILED,
	NEW_MESSAGE_ARRIVED,
	messageTypes,
	DELIVERY_INFORMATION_UPDATED
} from '../constants';
import {createFetchDeliveryAddressEffect} from '@devsnc/library-catalog-form';
import {t} from 'sn-translate';
export default {
	[REQUESTED_FOR_CHANGED]: createFetchDeliveryAddressEffect({
		method: 'GET',
		pathParams: ['user_id'],
		successActionType: DELIVERY_ADDRESS_UPDATED,
		errorActionType: DELIVERY_ADDRESS_UPDATE_FAILED
	}),
	[DELIVERY_ADDRESS_UPDATED]: ({action, dispatch}) => {
		const {
			payload: {result = ''}
		} = action;
		dispatch(DELIVERY_INFORMATION_UPDATED, {value: result});
	},
	[DELIVERY_ADDRESS_UPDATE_FAILED]: ({dispatch}) => {
		dispatch(DELIVERY_INFORMATION_UPDATED, {value: ''});
		dispatch(NEW_MESSAGE_ARRIVED, {
			type: messageTypes.ERROR,
			message: t('Unable to fetch delivery address')
		});
	}
};
