import {
	PRICE_AFFECTED,
	PRICE_UPDATED,
	PRICE_UPDATE_FAILED,
	NEW_MESSAGE_ARRIVED,
	messageTypes
} from '../constants';
import {createFetchPriceEffect} from '@devsnc/library-catalog-form';

export default {
	[PRICE_AFFECTED]: createFetchPriceEffect({
		method: 'POST',
		pathParams: ['sys_id'],
		dataParam: 'data',
		successActionType: PRICE_UPDATED,
		errorActionType: PRICE_UPDATE_FAILED
	}),
	[PRICE_UPDATED]: ({action, properties, updateProperties}) => {
		const {payload} = action;
		const {result = {}} = payload;
		const {
			price_display,
			recurring_frequency,
			recurring_price_display,
			recurring_price = 0,
			price = 0
		} = result;
		let {priceDetails = {}} = properties;
		let updatedPriceDetails = {
			...priceDetails,
			price: price ? price_display : '',
			recurringPrice: recurring_frequency ? recurring_price_display + " " + recurring_frequency : '',
			visible: price || recurring_price
		};
		updateProperties({
			priceDetails: updatedPriceDetails
		});
	},
	[PRICE_UPDATE_FAILED]: ({dispatch, action}) => {
		const {payload = {}} = action;
		const {
			data: {error}
		} = payload;
		dispatch(NEW_MESSAGE_ARRIVED, {
			type: messageTypes.ERROR,
			message: error.message
		});
	}
};
