import CatalogItemHandler from "../itemHandlers/CatalogItemHandler";
import RecordProducerItemHandler from "../itemHandlers/RecordProducerItemHandler";
import ItemHandler from "../itemHandlers/ItemHandler";

/**
 *
 * @param {String} type - Catalog item Type
 * @param {Function} dispatch - Dispatch function
 * @returns {ItemHandler}
 */
const createItemHandler = (type, dispatch) => {
    if (type === 'catalog_item') {
        return new CatalogItemHandler(dispatch);
    } else if(type === 'record_producer') {
        return new RecordProducerItemHandler(dispatch);
    }
    return new ItemHandler(dispatch);
}
export default createItemHandler;