import {VARIABLES_LAYOUT_CHANNEL, VARIABLES_LAYOUT_CHANNEL_VARIABLES_LAYOUT_PROVIDER} from '../channelConstants';
import { formViewDataConstants } from '@devsnc/library-catalog-form';
export const variablesLayoutFragmentResponseHandler = async (
	response = [],
	channelService = {}
) => {
	channelService.publish(VARIABLES_LAYOUT_CHANNEL)(
		VARIABLES_LAYOUT_CHANNEL_VARIABLES_LAYOUT_PROVIDER,
		response
	);
	return {
		formData: {
			[formViewDataConstants.VARIABLES_LAYOUT]: response
		}
	};
};
