export const createBeforeSubmitInterceptor = (context) => {
	const {coeffects} = context;
	const {state, action, properties, updateProperties} = coeffects;
	updateProperties({
		actionDetails: {
			...properties.actionDetails,
			disabled: true
		}
	});
	return {
		...context,
		coeffects: {
			...context.coeffects,
			action: {
				...action,
				payload: {
					...(action.payload || {}),
					...state.itemHandler.getSubmitPayload(state, properties)
				}
			}
		}
	};
};
