import {cloneDeep} from 'lodash';
import {
	VARIABLES_UPDATED,
	SN_CATALOG_FORM_CHANGED,
	QUANTITY_UPDATED,
	NEW_MESSAGE_ARRIVED,
	messageTypes,
	DELIVERY_INFORMATION_UPDATED,
	SPECIAL_INSTRUCTIONS_UPDATED,
	ATTACHMENT_UPLOADED,
	ATTACHMENT_DELETED,
	VARIABLE_REQUESTED_FOR,
	REQUESTED_FOR_CHANGED
} from '../constants';
import {GFORM_ADD_ERROR_MESSAGE, GFORM_ADD_INFO_MESSAGE} from '../gFormActions';
import {isAttrTrue} from '../../dataSources/fragments/fragmentUtils';
export default {
	[VARIABLES_UPDATED]: ({action, state, properties, updateProperties}) => {
		const {payload = {}} = action;
		const {name, propertyName, value} = payload;
		const {
			catalogItemDetails,
			quantityDetails,
			variables,
			formData
		} = properties;
		const {gForm, itemHandler} = state;
		if (propertyName === 'value' && itemHandler) {
			updateProperties({
				formData: {
					...formData,
					encodedRecord: gForm.getEncodedRecord(),
					serializedChanges: JSON.stringify(gForm.serialize())
				}
			});
			const field = variables[name];
			if (field.type === VARIABLE_REQUESTED_FOR) {
				dispatch(REQUESTED_FOR_CHANGED, {
					user_id: value
				});
			}
			field.value = value;
			itemHandler.handleVariableChange(
				{
					catalogItemDetails,
					quantityDetails,
					gForm
				},
				field
			);
		}
	},
	[SN_CATALOG_FORM_CHANGED]: ({
		updateProperties,
		properties,
		action: {payload = {}}
	}) => {
		const {hideRequestedFor} = properties;
		const requestedForDetails = isAttrTrue(hideRequestedFor)
			? Object.values(payload.fields || {}).find(
					(variable) => variable.type === VARIABLE_REQUESTED_FOR
			  ) || {}
			: {};
		updateProperties({
			variables: payload.fields,
			requestedForDetails
		});
	},
	[QUANTITY_UPDATED]: ({
		updateProperties,
		properties,
		state,
		action: {payload = {}}
	}) => {
		const {itemHandler, gForm} = state;
		const {value = 1} = payload;
		const {catalogItemDetails = {}, quantityDetails = {}} = properties;
		if (quantityDetails.value === value) {
			return;
		}
		quantityDetails.value = value;
		if (itemHandler) {
			itemHandler.handleQuantityChange({
				catalogItemDetails,
				quantityDetails,
				gForm
			});
		}
		updateProperties({
			quantityDetails: cloneDeep(quantityDetails)
		});
	},
	[NEW_MESSAGE_ARRIVED]: ({dispatch, action: {payload = {}}}) => {
		const {type = messageTypes.INFORMATION, message} = payload;
		let actionName = GFORM_ADD_INFO_MESSAGE;
		if (type === messageTypes.ERROR) {
			actionName = GFORM_ADD_ERROR_MESSAGE;
		}
		dispatch(actionName, {message});
	},
	[DELIVERY_INFORMATION_UPDATED]: ({
		properties,
		action: {payload = {}},
		updateProperties
	}) => {
		const {deliveryInformation = {}} = properties;
		const {value = ''} = payload;
		updateProperties({
			deliveryInformation: {
				...deliveryInformation,
				value
			}
		});
	},
	[SPECIAL_INSTRUCTIONS_UPDATED]: ({
		properties,
		action: {payload = {}},
		updateProperties
	}) => {
		const {specialInstructions = {}} = properties;
		const {value = ''} = payload;
		updateProperties({
			specialInstructions: {
				...specialInstructions,
				value
			}
		});
	},
	[ATTACHMENT_UPLOADED]: ({
		properties,
		action: {payload = {}},
		updateProperties
	}) => {
		const {sysId} = payload;
		if (sysId) {
			const {attachmentDetails} = properties;
			const {value = []} = attachmentDetails;
			updateProperties({
				attachmentDetails: {
					...attachmentDetails,
					value: [...value, sysId]
				}
			});
		}
	},
	[ATTACHMENT_DELETED]: ({
		properties,
		action: {payload = {}},
		updateProperties
	}) => {
		const {sysId} = payload;
		if (sysId) {
			const {attachmentDetails} = properties;
			const {value = []} = attachmentDetails;
			updateProperties({
				attachmentDetails: {
					...attachmentDetails,
					value: value.filter((id) => id != sysId)
				}
			});
		}
	}
};
