export const excludeVariableFromLayout = (
	variablesLayout = [],
	variables = {},
	typesToExclude = []
) =>
	variablesLayout.filter((layoutItem) => {
		if (
			layoutItem.type == 'container' ||
			layoutItem.type === 'checkbox_container'
		) {
			let columns = layoutItem.columns || [];
			columns = columns.map((column) => {
				column.fields = column.fields.filter((field) => {
					return !typesToExclude.includes((variables[field.name] || {}).type);
				});
				return column;
			});
			const shouldInlcude = columns.some((column) => {
				return (column.fields || []).length > 0;
			});
			return shouldInlcude;
		} else {
			return !typesToExclude.includes((variables[layoutItem.name] || {}).type);
		}
	});
