import ItemHandler from './ItemHandler';

export default class RecordProducerItemHandler extends ItemHandler {
	// eslint-disable-next-line no-unused-vars
	handleVariableChange(variable = {}) {
		// eslint-disable-next-line no-console
		console.info('Variable change ignored for record producer');
	}
	// eslint-disable-next-line no-unused-vars
	handleQuantityChange(value = 1) {
		// eslint-disable-next-line no-console
		console.info('Variable change ignored for record producer');
	}
	getSubmitPayload(state = {}, properties = {}) {
		const {gForm} = state;
		const {
			catalogItemDetails,
			targetRecord = {},
		} = properties;
		const payload = {
			sysId: catalogItemDetails.sysId,
			data: {
				sysparm_item_guid: targetRecord.sysId,
				variables: (gForm.serialize() || []).reduce((acc, field) => {
					acc[field.variable_name] = field.value;
					return acc;
				}, {})
			},
			actionName: 'submit_producer'
		};
		return payload;
	}
}
