import ItemHandler from './ItemHandler';
import {PRICE_AFFECTED} from '../sn-catalog-form-connected/constants';

export default class CatalogItemHandler extends ItemHandler {
	_getPayloadForPriceAffected(data = {}) {
		const {gForm, quantityDetails = {}, catalogItemDetails = {}} = data;
		return {
			sys_id: catalogItemDetails.sysId,
			data: {
				variables: {
					...gForm
						.serialize()
						.reduce(
							(acc, item) => ({...acc, [item.variable_name]: item.value}),
							{}
						)
				},
				sysparm_quantity: quantityDetails.value,
				sysparm_id: catalogItemDetails.sysId
			}
		};
	}
	// eslint-disable-next-line no-unused-vars
	handleVariableChange(state = {}, field = {}) {
		field.hasPriceImplecations &&
			this.dispatch(PRICE_AFFECTED, this._getPayloadForPriceAffected(state));
	}
	handleQuantityChange(data) {
		this.dispatch(PRICE_AFFECTED, this._getPayloadForPriceAffected(data));
	}
	getSubmitPayload(state = {}, properties = {}) {
		const {gForm} = state;
		const {
			catalogItemDetails,
			quantityDetails,
			deliveryInformation,
			specialInstructions,
			requestedForDetails,
			targetRecord = {},
			requestedForId = ''
		} = properties;
		const payload = {
			sysId: catalogItemDetails.sysId,
			data: {
				variables: (gForm.serialize() || []).reduce((acc, field) => {
					acc[field.variable_name] = field.value;
					return acc;
				}, {}),
				sysparm_quantity: quantityDetails.value || 1,
				delivery_address: deliveryInformation.value,
				special_instructions: specialInstructions.value,
				sysparm_requested_for: requestedForDetails.value || requestedForId,
				sysparm_item_guid: targetRecord.sysId
			},
			actionName: 'order_now'
		};
		return payload;
	}
}
