import {createHttpEffect} from '@servicenow/ui-effect-http';
import _ from 'lodash';
import {
	SUBMIT_CLICKED,
	SUBMIT_VALIDATED,
	ITEMS_SUBMIT_URL,
	SUBMITTED,
	SUBMIT_FAILED,
	NEW_MESSAGE_ARRIVED,
	messageTypes,
	REQUEST_GENERATED
} from '../constants';
import {createBeforeSubmitInterceptor} from '../../interceptors';

export default {
	[SUBMIT_CLICKED]: ({state, updateProperties, properties}) => {
		const {gForm} = state;
		gForm && gForm.submit();
	},
	[SUBMIT_VALIDATED]: {
		...createHttpEffect(ITEMS_SUBMIT_URL, {
			method: 'POST',
			pathParams: ['sysId', 'actionName'],
			dataParam: 'data',
			successActionType: SUBMITTED,
			errorActionType: SUBMIT_FAILED
		}),
		interceptors: [createBeforeSubmitInterceptor]
	},
	[SUBMITTED]: ({dispatch, action: {payload = {}}}) => {
		const {result = {}} = payload;
		dispatch(REQUEST_GENERATED, {
			table: result.table,
			sysId: result.sys_id,
			number: result.number
		});
	},
	[SUBMIT_FAILED]: ({action, dispatch, properties, updateProperties}) => {
		const {payload = {}} = action;
		updateProperties({
			actionDetails: {
				...properties.actionDetails,
				disabled: false
			}
		});
		const message = payload.message || _.get(payload, 'data.result.errMsg');
		dispatch(NEW_MESSAGE_ARRIVED, {
			type: messageTypes.ERROR,
			message
		});
	}
};
