import {createQueryFragment} from '@devsnc/library-catalog-form';
import {VARIABLES_CHANNEL} from './channelConstants';
import {VARIABLES_FRAGMENT_PREFIX} from './queryConstants';
import {variablesFragmentResponseHandler} from './responseHandlers';

const VariablesFragment = createQueryFragment({
	prefix: VARIABLES_FRAGMENT_PREFIX,
	variables: [],
	queryTemplate: () => {
		return `
            ${VARIABLES_FRAGMENT_PREFIX} {
                ... on AppCatalog_MultiRowVariableSetType {
                    type
                    name
                    label
                    containerType
                    canRead
                    canWrite
                    canCreate
                    variableName
                    id
                    maxRows
                    fields {
                        name
                        id
                        label
                        unique
                    }
                    rowData {
                        row {
                        id
                        value
                        displayValue
                        }
                    }
                }
                ... on AppCatalog_SingleRowVariableSetType {
                    type
                    id
                    name
                    label
                    containerType
                    visible
                    canRead
                    canWrite
                    canCreate
                    variableName
                }
                ... on AppCatalog_ChoiceQuestionElementType {
                    type
                    id
                    name
                    variableName
                    label
                    order
                    catalogItem
                    variableSet
                    mandatory
                    readOnly
                    visible
                    canRead
                    canWrite
                    canCreate
                    variableAttributes
                    defaultValue
                    lookupTable
                    lookupValue
                    lookupLabel
                    includeNone
                    lookupUnique
                    choiceTable
                    choiceField
                    choiceDirection
                    choices {
                        displayValue: label
                        value
                    }
                    dependentField
                    referringTable
                    referringRecordId
                    hasPriceImplecations
                }
                ... on AppCatalog_ReferenceQuestionElementType {
                    type
                    id
                    name
                    variableName
                    label
                    order
                    catalogItem
                    variableSet
                    mandatory
                    readOnly
                    visible
                    canRead
                    canWrite
                    canCreate
                    reference
                    referenceQual
                    listTable
                    useReferenceQualifier
                    variableAttributes
                    defaultValue
                    referringTable
                    referringRecordId
                    hasPriceImplecations
                }
                ... on AppCatalog_ContainerQuestionElementType {
                    type
                    id
                    name
                    variableName
                    label
                    order
                    visible
                    canRead
                    canWrite
                    canCreate
                    catalogItem
                    variableSet
                    displayTitle
                    layout
                    containerType
                    hasPriceImplecations
                }
                ... on AppCatalog_AttachmentQuestionElementType {
                    type
                    id
                    name
                    variableName
                    label
                    order
                    catalogItem
                    variableSet
                    mandatory
                    readOnly
                    visible
                    canRead
                    canWrite
                    canCreate
                    variableAttributes
                    defaultValue
                    contentType
                }
                ... on AppCatalog_MacroQuestionElementType {
                    type
                    id
                    name
                    variableName
                    label
                    order
                    catalogItem
                    variableSet
                    mandatory
                    canRead
                    canWrite
                    canCreate
                    variableAttributes
                    defaultValue
                    macroponentId
                }
                ... on AppCatalog_StandardQuestionElementType {
                    type
                    id
                    name
                    variableName
                    label
                    order
                    catalogItem
                    variableSet
                    mandatory
                    readOnly
                    visible
                    canRead
                    canWrite
                    canCreate
                    variableAttributes
                    defaultValue
                    exampleText
                    useConfirmation
                    regExp
                    canDecrypt
                    hasPriceImplecations
                }
                __typename
            }
            `;
	},
	responseHandler: variablesFragmentResponseHandler,
	channels: [VARIABLES_CHANNEL],
	children: []
});
export default VariablesFragment;
