import {actionTypes} from '@servicenow/ui-core';
import {t} from 'sn-translate';
import {isEmpty, merge} from 'lodash';
const {COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED} = actionTypes;
import {
	CATALOG_ITEM_CHANGED,
	SC_CAT_ITEM,
	FAILED_TO_FETCH_RESOURCES,
	RESOURCES_FETCHED,
	RESOURCES_FETCH_STARTED,
	RESOURCES_PROCESSED,
	FORM_ENVIRONMENT_CREATED,
	CATALOG_ITEM_READY,
	NEW_MESSAGE_ARRIVED,
	messageTypes,
	FORM_ENVIRONMENT_CREATION_FAILED,
	VARIABLES_UPDATED,
	SUBMIT_VALIDATED,
	VARIABLE_REQUESTED_FOR
} from '../constants';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import catalogItemDataSource from '../../dataSources/catalogItemDataSource';
import {initialProperties} from '../initialData';
import {createInitializeCatalogFormEnvironmentEffect} from '@devsnc/library-catalog-form';
import {SnFormModal} from '@devsnc/sn-scripted-modal';
import createItemHandler from '../../itemHandlers/ItemHandlerFactory';
import {excludeVariableFromLayout} from '../../utils';
import {isAttrTrue} from '../../dataSources/fragments/fragmentUtils';
import {G_FORM} from '@devsnc/library-catalog-form/src/constants';
import {GFORM_SET_VALUE} from '../gFormActions';
const updateData = (updateProperties, data) => {
	const processed = merge(initialProperties, data);
	const {
		catalogItemDetails = {},
		quantityDetails = {},
		attachmentDetails = {},
		priceDetails = {},
		targetRecord = {},
		actionDetails = {},
		deliveryInformation = {},
		specialInstructions = {},
		formData: veditor = {},
		userSession = {}
	} = processed;
	const formData = {
		tableName: SC_CAT_ITEM,
		sysId: catalogItemDetails.sysId,
		userSession
	};
	updateProperties({
		catalogItemDetails,
		quantityDetails,
		attachmentDetails,
		priceDetails,
		targetRecord,
		actionDetails,
		deliveryInformation,
		specialInstructions,
		formData,
		variables: veditor.fields,
		variablesLayout: veditor.variablesLayout
	});
};
export default {
	[COMPONENT_BOOTSTRAPPED]: ({dispatch, properties}) => {
		window.dispatch = dispatch;
		const {sysId = '', requestedForId = '', table} = properties;
		if (isEmpty(sysId)) {
			dispatch(FAILED_TO_FETCH_RESOURCES);
			return;
		}
		dispatch(CATALOG_ITEM_CHANGED, {
			sysId,
			requestedFor: requestedForId,
			table
		});
	},
	[COMPONENT_PROPERTY_CHANGED]: ({dispatch, action, properties}) => {
		const {
			payload: {name, value, previousValue}
		} = action;
		const {sysId = '', requestedForId = '', table = ''} = properties;
		const watchedProperties = ['sysId', 'requestedForId'];
		if (!watchedProperties.includes(name)) {
			return;
		}
		if (previousValue != value) {
			if (isEmpty(sysId)) {
				// is there is no sys_id, reset props
				dispatch(RESOURCES_FETCH_STARTED);
				dispatch(FAILED_TO_FETCH_RESOURCES);
				return;
			}
			dispatch(CATALOG_ITEM_CHANGED, {
				sysId,
				requestedFor: requestedForId,
				table
			});
		}
	},
	[CATALOG_ITEM_CHANGED]: createGraphQLEffect(
		catalogItemDataSource.getQuery(),
		{
			variableList: ['sysId', 'requestedFor', 'table'],
			startActionType: RESOURCES_FETCH_STARTED,
			successActionType: RESOURCES_FETCHED,
			errorActionType: FAILED_TO_FETCH_RESOURCES
		}
	),
	[RESOURCES_FETCH_STARTED]: ({updateProperties}) => {
		updateData(updateProperties, initialProperties);
	},
	[RESOURCES_FETCHED]: async ({
		action,
		updateProperties,
		dispatch,
		properties
	}) => {
		const {payload = {}} = action;
		if (payload.errors && payload.errors.length > 0) {
			dispatch(FAILED_TO_FETCH_RESOURCES, payload.errors);
		} else {
			const data = await catalogItemDataSource.resolve(payload);
			const {hideRequestedFor = false, userSession = {}} = properties;
			let variablesLayout = data.formData.variablesLayout || [];
			if (isAttrTrue(hideRequestedFor)) {
				data.formData.variablesLayout = excludeVariableFromLayout(
					variablesLayout,
					data.formData.fields,
					[VARIABLE_REQUESTED_FOR]
				);
			}
			updateData(updateProperties, {...data, userSession});
			dispatch(RESOURCES_PROCESSED, {
				itemDetails: data.catalogItemDetails,
				formData: {
					// formModal: new SnFormModal(),
					sourceTable: SC_CAT_ITEM,
					sourceId: properties.sysId,
					...data.formData
				}
			});
		}
	},
	[RESOURCES_PROCESSED]: createInitializeCatalogFormEnvironmentEffect({
		dataParam: 'formData',
		successActionType: FORM_ENVIRONMENT_CREATED,
		errorActionType: FORM_ENVIRONMENT_CREATION_FAILED,
		formSubmittedActionType: SUBMIT_VALIDATED,
		variableUpdatedActionType: VARIABLES_UPDATED
	}),
	[FAILED_TO_FETCH_RESOURCES]: ({dispatch, action, updateProperties}) => {
		const {payload = {}} = action;
		const message = t('You are either not authorized or record is not valid.');
		updateProperties({
			isLoading: false,
			isError: true,
			isSuccess: false,
			errorMessage: message
		});
		dispatch(NEW_MESSAGE_ARRIVED, {
			type: messageTypes.ERROR,
			message: message
		});
	},
	[FORM_ENVIRONMENT_CREATED]: ({
		dispatch,
		properties,
		action,
		updateProperties,
		updateState
	}) => {
		const {payload = {}} = action;
		const {gForm, fields} = payload;
		const {
			catalogItemDetails = {},
			hideRequestedFor = false,
			formData = {},
			requestedForId = ''
		} = properties;
		let variablesLayout = payload.variablesLayout || [];
		let requestedForDetails = {};
		let isFieldsEmpty = false;
		if (Object.keys(fields).length == 0) {
			isFieldsEmpty = true;
		}
		if (isAttrTrue(hideRequestedFor)) {
			variablesLayout = excludeVariableFromLayout(variablesLayout, fields, [
				VARIABLE_REQUESTED_FOR
			]);
			requestedForDetails =
				Object.values(fields || {}).find(
					(variable) => variable.type === VARIABLE_REQUESTED_FOR
				) || {};
		}
		updateProperties({
			isLoading: false,
			isError: false,
			isSuccess: true,
			hasNoVariables: isFieldsEmpty,
			variables: fields,
			requestedForDetails,
			variablesLayout,
			formData: {
				...formData,
				encodedRecord: gForm.getEncodedRecord(),
				serializedChanges: JSON.stringify(gForm.serialize())
			}
		});
		const itemHandler = createItemHandler(catalogItemDetails.type, dispatch);
		updateState({
			gForm,
			itemHandler
		});
		dispatch(CATALOG_ITEM_READY, {
			id: properties.sysId
		});
	},
	[FORM_ENVIRONMENT_CREATION_FAILED]: ({
		dispatch,
		action,
		updateProperties
	}) => {
		const {payload = {}} = action;
		updateProperties({
			isLoading: false,
			isError: true,
			isSuccess: false,
			errorMessage: payload.error
		});
		dispatch(NEW_MESSAGE_ARRIVED, {
			type: messageTypes.ERROR,
			message: payload.error
		});
	}
};
